// src/pages/wholesaler/orders.js
import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import WholesalerLayout from '../../components/WholesalerLayout'; // <-- IMPORT WHOLESALER LAYOUT
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import {
  Pagination,
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertTriangle, Loader2 } from "lucide-react";

// Helper to format currency
const formatPrice = (p) => `â‚¹${Number(p || 0).toLocaleString('en-IN')}`;

const ORDERS_PER_PAGE = 10;

export default function WholesalerOrdersPage() {
  const router = useRouter();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // Pagination state
  const [page, setPage] = useState(1);
  const [totalOrders, setTotalOrders] = useState(0);
  const totalPages = Math.ceil(totalOrders / ORDERS_PER_PAGE);

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        setError(null);
        
        const token = localStorage.getItem("token"); //
        if (!token) {
          throw new Error("No authorization token found. Please log in again.");
        }

        const authHeaders = {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}` //
        };

        // --- THIS IS THE CHANGE ---
        const res = await fetch(
          `/api/wholesaler/orders?page=${page}&limit=${ORDERS_PER_PAGE}&sort=createdAt:desc`, 
          { headers: authHeaders }
        );
        
        if (!res.ok) {
           const errData = await res.json();
           throw new Error(errData.error || 'Failed to fetch orders');
        }
        const data = await res.json();
        setOrders(data.items);
        setTotalOrders(data.total);
      } catch (err) {
        console.error(err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [page]); // Re-fetch when page changes

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setPage(newPage);
    }
  };

  return (
    // --- THIS IS THE CHANGE ---
    <WholesalerLayout>
      <Card>
        <CardHeader>
          <CardTitle>Orders List</CardTitle>
          <CardDescription>
            Here are all the orders placed for your products.
          </CardDescription>
        </CardHeader>
        <CardContent>
          {loading && (
            <div className="flex justify-center items-center py-10">
              <Loader2 className="h-8 w-8 animate-spin" />
            </div>
          )}
          
          {error && (
            <Alert variant="destructive" className="mb-6">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>Error</AlertTitle>
              {/* --- THIS IS THE CHANGE --- */}
              <AlertDescription>
                {error}
                <p className="text-xs mt-2">
                  Make sure you are logged in as a WHOLESALER.
                </p>
              </AlertDescription>
            </Alert>
          )}

          {!loading && !error && (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Order ID</TableHead>
                  <TableHead>Customer</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Amount</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {orders.length > 0 ? (
                  orders.map(order => (
                    <TableRow key={order._id}>
                      <TableCell className="font-medium">#{order._id.slice(-6)}</TableCell>
                      <TableCell>{order.customer?.name}</TableCell>
                      <TableCell>{new Date(order.createdAt).toLocaleDateString()}</TableCell>
                      <TableCell>
                        <Badge 
                          variant={order.status === 'delivered' ? 'default' : 'secondary'}
                          className={
                            order.status === 'delivered' ? 'bg-green-600 text-white' :
                            (order.status === 'cancelled' || order.status === 'refunded' ? 'bg-red-600 text-white' : 'bg-gray-500 text-white')
                          }
                        >
                          {order.status}
                        </Badge>
                      </TableCell>
                      <TableCell className="text-right">{formatPrice(order.total)}</TableCell>
                    </TableRow>
                  ))
                ) : (
                  <TableRow>
                    <TableCell colSpan="5" className="text-center text-muted-foreground">
                      No orders found.
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          )}

          {/* Pagination Controls */}
          {totalPages > 1 && (
            <Pagination className="mt-6">
              <PaginationContent>
                <PaginationItem>
                  <PaginationPrevious 
                    href="#"
                    onClick={(e) => { e.preventDefault(); handlePageChange(page - 1); }}
                    disabled={page === 1}
                  />
                </PaginationItem>
                
                <PaginationItem>
                  <PaginationLink href="#" isActive>
                    {page}
                  </PaginationLink>
                </PaginationItem>
                
                <PaginationItem>
                  <PaginationNext 
                    href="#"
                    onClick={(e) => { e.preventDefault(); handlePageChange(page + 1); }}
                    disabled={page === totalPages}
                  />
                </PaginationItem>
              </PaginationContent>
            </Pagination>
          )}

        </CardContent>
      </Card>
    </WholesalerLayout>
  );
}
